body { 
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  background: linear-gradient(to right, rgb(199 69 120) 0%, rgb(80 191 89) 100%); /* Standard syntax */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}   

.square { 
  height: 100px;
  width: 110px; 
  margin: 8px 8px;
  font-size: 36px;  
  background-color: white;
  border-radius: 14px;
  border: rgb(187 146 146);
  text-align: center;
  justify-content: center;
  color: #116a8e;
} 

.oneSignal {
  font-weight: 300; 
  background: -webkit-linear-gradient(rgb(21 7 1), rgb(199 69 120));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; 
}

.secSignal {
  font-weight: 300; 
  background: -webkit-linear-gradient(rgb(125 24 29), rgb(84 24 191));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}  

.reset-btn {
  width: 225px;
  color: rgb(148 37 42);
  background-image: linear-gradient(rgb(236 211 152), rgb(216 190 191)); 
  height: 30px;
} 

.Panel {
  width: 500px;
  height: 600px;
  display: flex;
  justify-content: center; 
  flex-wrap: wrap;
  align-content: center;
} 

.Message {
  font: 38px "Century Gothic", Futura, sans-serif; 
  width: 500px;
  font-weight: 600; 
  text-align: center;
} 